              '983',
            ],
          ],
          // 
          [
            ['', '', '', ''],

            ['209', '210', '211', '212'],
          ],
        ],
      },
      buyer_district_list: [],
      buyer_zip_code_list: [],
      recipient_district_list: [],
      recipient_zip_code_list: [],
      buyer: {
        name: '',
        tel: '',
        email: '',
      },
      invoice: {
        type: 'personal_invoice',
        carrier: 'no_carrier',
        address: {
          county: '',
          district: '',
          zip_code: '',
          full_address: '',
        },
      },
      recipient: {
        takeaway: false,
        name: '',
        tel: '',
        address: {
          county: '',
          district: '',
          zip_code: '',
          full_address: '',
        },
        recipient_alternative: {
          available: false,
          name: '',
        },
        anonymous: false,
        estimated_date: '',
        estimated_time: 'default',
        card_content: '',
        order_remark: '',
      },
      invoice_type_options: [
        {
          chinese: '',
          english: 'personal_invoice',
        },
        {
          chinese: '',
          english: 'donate_invoice',
        },
        {
          chinese: '',
          english: 'company_invoice',
        },
      ],
      invoice_carrier_options: [
        {
          chinese: '',
          english: 'no_carrier',
        },
        {
          chinese: '',
          english: 'barcode_carrier',
        },
        {
          chinese: '',
          english: 'natural_carrier',
        },
      ],
      estimated_time_options: [
        {
          chinese: '',
          english: 'default',
        },
        {
          chinese: '(08:00~13:00)',
          english: 'morning',
        },
        {
          chinese: '(14:00~18:00)',
          english: 'afternoon',
        },
      ],
    };
  },
  methods: {
    go_page(pageString) {
      this.$router.push(`/${pageString}`);
    },
    create_buyer_district_list(county) {
      const countyIndex = this.area_data.counties
        .findIndex((item) => item === county);
      [this.buyer_district_list, this.buyer_zip_code_list] = this
        .area_data.districts[countyIndex];
    },
    create_buyer_zip_code(district) {
      const countyIndex = this.area_data.counties
        .findIndex((item) => item === this.invoice.address.county);
      const districtIndex = this.area_data.districts[countyIndex][0]
        .findIndex((item) => item === district);
      this.invoice.address.zip_code = this.area_data
        .districts[countyIndex][1][districtIndex];
    },
    create_recipient_district_list(county) {
      const countyIndex = this.area_data.counties
        .findIndex((item) => item === county);
      [this.recipient_district_list, this.recipient_zip_code_list] = this
        .area_data.districts[countyIndex];
    },
    create_recipient_zip_code(district) {
      const countyIndex = this.area_data.counties
        .findIndex((item) => item === this.recipient.address.county);
      const districtIndex = this.area_data.districts[countyIndex][0]
        .findIndex((item) => item === district);
      this.recipient.address.zip_code = this.area_data
        .districts[countyIndex][1][districtIndex];
    },
    toggle_takeaway(boolean) {
      this.toggle_buyer_data(!JSON.parse(boolean));
    },
    toggle_buyer_data(checkBoxStatus) {
      if (checkBoxStatus) {
        this.recipient.name = this.buyer.name;
        this.recipient.tel = this.buyer.tel;
        this.create_recipient_district_list(this.invoice.address.county);
        this.recipient.address = this.invoice.address;
        this.create_recipient_zip_code(this.invoice.address.district);
      } else {
        this.recipient.name = '';
        this.recipient.tel = '';
        this.recipient_district_list = [];
        this.recipient_zip_code_list = [];
        this.recipient.address = {
          county: '',
          district: '',
          zip_code: '',
          full_address: '',
        };
      }
    },
    go_next_step() {
      this.this_order.buyer = JSON.parse(JSON.stringify(this.buyer));
      this.this_order.invoice = JSON.parse(JSON.stringify(this.invoice));
      this.this_order.recipient = JSON.parse(JSON.stringify(this.recipient));
      this.go_page('front/checkout/checkout3');
    },
  },
  created() {
    this.this_order.buyer = {
      name: '',
      tel: '',
      email: '',
    };
    this.this_order.invoice = {
      type: 'personal_invoice',
      carrier: 'no_carrier',
      address: {
        county: '',
        district: '',
        zip_code: '',
        full_address: '',
      },
    };
    this.this_order.recipient = {
      takeaway: false,
      name: '',
      tel: '',
      address: {
        county: '',
        district: '',
        zip_code: '',
        full_address: '',
      },
      recipient_alternative: {
        available: false,
        name: '',
      },
      anonymous: false,
      estimated_date: '',
      estimated_time: 'default',
      card_content: '',
      order_remark: '',
    };
  },
  mounted() {
    this.$bus.$emit('update_step', 2);
    // this.axios.get('https://api.nlsc.gov.tw/other/ListCounty')
    //   .then((response) => {
    //     const { parseString } = require('xml2js');
    //     parseString(response.data, (err, result) => {
    //       console.dir(result);
    //     });
    //   });
  },
};
</script>
